@font-face {
  font-family: "Brown-bold";
  src: url("fonts/Brown-Bold.otf");
}

@font-face {
  font-family: "Brown-regular";
  src: url("fonts/Brown-Regular.otf");
}

* {
  font-family: Brown-regular;
}

body {
  height: 100vh;
  width: 100vw;
  background-color: #f4f4f4;
}

h1,
body,
p {
  margin: 0;
}

p {
  padding: 5px;
}

header {
  text-align: center;
  color: #7878f4;
  margin-bottom: 20px;
  font-size: 28px;
}

.main {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

aside {
  width: 210px;
  height: 80vh;
  overflow-y: scroll;
  border-radius: 5px;
  box-shadow: 1px 1px 5px grey;
}

main {
  height: 80vh;
  width: 70vw;
  border: 0.5px solid #dadada;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 1px 1px 10px grey;
}

.dataContainer {
  box-sizing: border-box;
  height: 10vh;
  width: auto;
}

.photoContainer {
  height: 70vh;
  width: 70vw;
}

.photoContainer img {
  max-width: 100%;
  max-height: 100%;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #7878f4;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #6767e0;
}
